import { Injectable } from '@angular/core';
import {Option} from '../model/option.model';
import {HttpClient} from '@angular/common/http'
import {Observable} from 'rxjs'
@Injectable()
export class OptionService {
  public API:string="http://42.117.5.115:8049/coreAPI/tbl_qldtlt_option";
  public API2:string ="http://42.117.5.115:8049/coreAPI/tbl_qldtlt_option/find?pageSize=10";
  public options:Option[]=[];
  constructor(public http:HttpClient) { }
  Get(page:number):Observable<Option[]>{
    return this.http.get<Option[]>(`${this.API2}&page=${page}`);
  }
  GetId(id: number): Observable<Option> {
    const url = `${this.API}/${id}`;
    console.log(url);
    return this.http.get<Option>(url);
  }
  Post(op:Option):Observable<Option>{
    return this.http.post<Option>(this.API,op);
  }
  Delete(id:number):Observable<number>{
    return this.http.delete<number>(`${this.API}/${id}`);
  }
  Put(op:Option):Observable<Option>{
    return this.http.put<Option>(this.API,op);
  }
}
