import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Observable } from 'rxjs'
import { Student } from '../model/student.class'
@Injectable()
export class StudentService {
  public API: string = "http://5bbad133828fb30013ef6efd.mockapi.io/api/student";
  constructor(public http: HttpClient) { }
  Get(): Observable<Student[]> {
    return this.http.get<Student[]>(this.API);
  }
  Post(sv: Student): Observable<Student> {
    return this.http.post<Student>(this.API, sv);
  }
  handleError(err) {
    if (err.error instanceof Error) {
      console.log("Client");
    }
    else {
      console.log("server");
    }
  }
  Put(sv: Student): Observable<Student> {
    return this.http.put<Student>(`${this.API}/${sv.id}`, sv);
  }
  Delete(id: number): Observable<Student> {
    return this.http.delete<Student>(`${this.API}/${id}`);
  }
}
