import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http'
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/delay';
import {OptionGroup} from '../model/optiongroup.model'
import {debounceTime} from 'rxjs/internal/operators/debounceTime';
import {map} from 'rxjs/operators';
import {disableDebugTools} from '@angular/platform-browser';

@Injectable()
export class OptionGroupService {
    public API = 'http://42.117.5.115:8049/coreAPI/tbl_optiongroup';
    public API2 = 'http://42.117.5.115:8049/coreAPI/tbl_optiongroup/find?pageSize=10';
    public APICount = 'http://42.117.5.115:8049/coreAPI/tbl_optiongroup/count';
    public APIop = 'http://42.117.5.115:8049/coreAPI/tbl_option/find?pageSize=5';
    public APIop1 = 'http://42.117.5.115:8049/coreAPI/tbl_option';
    public APICountOp = 'http://42.117.5.115:8049/coreAPI/tbl_option/count';
    public APIsearchAutoComplete = 'http://42.117.5.115:8049/coreAPI/tbl_optiongroup/find?page=0';

    constructor(public http: HttpClient) {
    }

    Get(page: number): Observable<OptionGroup[]> {
        return this.http.get<OptionGroup[]>(`${this.API2}&page=${page}`);
    }

    getOption(page: number, optionGroupId: number): Observable<OptionGroup[]> {
        return this.http.get<OptionGroup[]>(`${this.APIop}&page=${page}&optionGroupId={"id":"${optionGroupId}"}`);
    }

    getId(id: number): Observable<OptionGroup> {
        return this.http.get<OptionGroup>(`${this.API}/${id}`)
    }
    getIdChild(id: number): Observable<OptionGroup> {
        return this.http.get<OptionGroup>(`${this.APIop1}/${id}`)
    }

    Post(op: OptionGroup): Observable<OptionGroup> {
        return this.http.post<OptionGroup>(this.API, op);
    }

    postChildOption(op: OptionGroup): Observable<OptionGroup> {
        return this.http.post<OptionGroup>(this.APIop1, op);
    }

    Put(op: OptionGroup): Observable<OptionGroup> {
        return this.http.put<OptionGroup>(this.API, op);
    }

    putChildOption(op: OptionGroup): Observable<OptionGroup> {
        return this.http.put<OptionGroup>(this.APIop1, op);
    }

    Delete(id: number): Observable<number> {
        return this.http.delete<number>(`${this.API}/${id}`);
    }

    deleteChildOption(id: number): Observable<number> {
        return this.http.delete<number>(`${this.APIop1}/${id}`);
    }

    Count(ma: string): Observable<OptionGroup> {
        return this.http.get<OptionGroup>(`${this.APICount}?&code=${ma}`)
    }

    countOption(optionGroupId: number, name: string) {
        return this.http.get<OptionGroup>(`${this.APICountOp}?name=${name}&optionGroupId={"id":"${optionGroupId}"}`)
    }

    Search(page: number, ma: string): Observable<OptionGroup[]> {
        return this.http.get<OptionGroup[]>(`${this.API2}&page=${page}&code=${ma}`)
    }

    Search1(term) {
        const listName = this.http.get(`${this.APIsearchAutoComplete}&name=${term}`)
            .pipe(
                map(
                    (data: any) => {
                        return (
                            data.length !== 0 ? data : []
                        );
                    }
                ));

        return listName;
    }

    searchOption(page: number, name: string, optionGroupId: number): Observable<OptionGroup[]> {
        return this.http.get<OptionGroup[]>(`${this.APIop}&page=${page}&name=${name}&optionGroupId={"id":"${optionGroupId}"}`)
    }
}
