import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Menu} from '../model/menu.model';
import {Observable} from 'rxjs'
import {tap, map} from 'rxjs/operators';

@Injectable()
export class MenuService {
    public API: string = 'http://42.117.5.115:8049/coreAPI/tbl_menu';
    public APISearch: string = 'http://42.117.5.115:8049/coreAPI/tbl_menu/find?pageSize=10';
    public APIcmb: string = 'http://42.117.5.115:8049/coreAPI/tbl_menu/find?pageSize=-1';
    // public APISearch2:string='http://42.117.5.115:8049/coreAPI/tbl_menu/find?pageSize=10&page=0';
    public count: string = 'http://42.117.5.115:8049/coreAPI/tbl_menu/count'
    public menu: Menu[] = [];

    constructor(public http: HttpClient) {
    }

    Get(page: number): Observable<Menu[]> {
        return this.http.get<Menu[]>(`${this.APISearch}&page=${page}`);
    }
    Get1(page: number): Observable<Menu[]> {
        return this.http.get<Menu[]>(`${this.APIcmb}&page=${page}`);
    }

    Count(ten: string): Observable<Menu> {
        return this.http.get<Menu>(`${this.count}?ten=${ten}`);
    }

    SearchMenu(page: number, ten: string): Observable<Menu[]> {
        return this.http.get<Menu[]>(`${this.APISearch}&page=${page}&ten=${ten}`)
    }

    GetId(id: number): Observable<Menu> {
        const url1 = `${this.API}/${id}`;
        console.log(url1);
        return this.http.get<Menu>(url1);
    }

    Post(mn: Menu): Observable<Menu> {
        return this.http.post<Menu>(this.API, mn);
    }

    Put(mn: Menu): Observable<Menu> {
        return this.http.put<Menu>(this.API, mn);
    }

    Delete(id: number): Observable<number> {
        return this.http.delete<number>(`${this.API}/${id}`);
    }
}
