import { Injectable } from '@angular/core';
import { Observable } from 'rxjs'
import { HttpClient } from '@angular/common/http'
import { QuanLiModel } from '../model/quanli.model'

@Injectable()
export class QuanliService {
  public API: string = 'http://42.117.5.115:8049/coreAPI/tbl_menu';
  constructor(public http: HttpClient) { }

  Get(): Observable<QuanLiModel[]> {
    return this.http.get<QuanLiModel[]>(this.API);
  }

  Post(ql: QuanLiModel): Observable<QuanLiModel> {
    return this.http.post<QuanLiModel>(this.API, ql);
  }
  Put(ql: QuanLiModel): Observable<QuanLiModel> {
    return this.http.put<QuanLiModel>(this.API, ql);
  }
  Delete(id: number): Observable<number> {
    return this.http.delete<number>(`${this.API}/${id}`);
  }
}
